import {createRouter, createWebHistory} from 'vue-router'


import Home from '@/pages/Home.vue'



const routers = createRouter({
    history: createWebHistory(),
    routes: [
        { path: '/', name: 'HomePage', component: Home},
        { path: '/about', name: 'AboutPage', component: () => import('@/pages/About.vue')},
        { path: '/about/:id', name: 'CarItem', component: () => import('@/pages/CarItem.vue')},
        { path: '/home/:id', name: 'AutorItem', component: () => import('@/pages/Autoritem.vue')},
        { path: '/extra', name: 'extraPage', component: () => import('@/pages/Extra.vue')},
        { path: '/law', name: 'lawPage', component: () => import('@/pages/Law.vue')},

    ]
})  


export default routers